<template>
  <div class="signin">
    <div>
      <SelectCountry></SelectCountry>
      <div class="logo"><img src="../assets/newsage/img/signin/logo.png" alt="" /></div>
      <div>
        <div style="display: flex; justify-content: center">
          <div class="bodybg">
            <div >
              <div class="signin_title">{{HTML_SIGIN[$store.state.language].TITLE}}</div>
              <div class="signin_title_h2">{{HTML_SIGIN[$store.state.language].PROMPT}}</div>
              <div class="divbuttonlin">
                  <div class="divbutton">
                    <div class="button_title" @click="clickAuthorization">
                     {{HTML_SIGIN[$store.state.language].BUTTON}}
                    </div>
                  </div>
                </div>
                <div class="hr"></div>
              <div class="body">
                <div class="body_title">
                  {{HTML_SIGIN[$store.state.language].DESCRIPTION}}
                </div>
                <div><input type="text" class="body_input" :value="selectAddress"  @blur="inputvalueChange" /></div>
                <div class="divbuttonlin">
                  <div class="div_lin_button">
                    <div class="div_lin_button_title" @click="clickSignin">{{HTML_SIGIN[$store.state.language].BUTTON2}}
                    </div>
                  </div>
                </div>
                <div class="hr"></div>
                <div class="prompt">
                 {{HTML_SIGIN[$store.state.language].DESCRIPTION2}}
                </div>
                <div class="prompt" @click="clickGoSignUp">
                  <img src="../assets/newsage/img/signin/add.svg" alt="" style="width: 2rem;">
                  <samp>{{HTML_SIGIN[$store.state.language].BUTTON3}}</samp>
                </div>
              </div>
            </div>
          </div>
        </div>
        
      </div>

      <div></div>
    </div>
  </div>
</template>

<script>
export default {
  components:{
    SelectCountry
  },
  computed: {
  },
  data() {
    return {
      selectAddress:'',
      HTML_SIGIN,
      factory: this.$store.state.factory,
      newsage: this.$store.state.newsage,
    };
  },
  methods: {
       ...mapActions([
      ACTION_TYPES.INIT_NEWSAGE,
      ACTION_TYPES.INIT_FACTORY,
      ACTION_TYPES.INIT_NEWSAGE_ROUTER,
      "displayLoding",
      "randomDisplayData",
      "displayTips"
    ]),
    //游戏启动次数
    [FACTORY_TYPES.FACTORY_LAST_ID]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: {},
        methodName: FACTORY_TYPES.FACTORY_LAST_ID,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //代币地址
    [FACTORY_TYPES.FACTORY_DETO_ADDRESS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: {},
        methodName: FACTORY_TYPES.FACTORY_DETO_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //路由工厂地址
    [FACTORY_TYPES.FACTORY_NEWSAGE_ROUTER_FACTORY_ADDRESS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: {},
        methodName: FACTORY_TYPES.FACTORY_NEWSAGE_ROUTER_FACTORY_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //newsage工厂地址
    [FACTORY_TYPES.FACTORY_NEWSAGE_FACTORY_ADDRESS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: {},
        methodName: FACTORY_TYPES.FACTORY_NEWSAGE_FACTORY_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //WETH地址
    [FACTORY_TYPES.FACTORY_WETH_ADDRESS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: {},
        methodName: FACTORY_TYPES.FACTORY_WETH_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //uniswap 资金池地址
    [FACTORY_TYPES.FACTORY_UNISWAPV2_PAIR_ADDRESS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: {},
        methodName: FACTORY_TYPES.FACTORY_UNISWAPV2_PAIR_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //uniswap 路由地址
    [FACTORY_TYPES.FACTORY_UNISWAPV2_ROUTER02_ADDRESS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: {},
        methodName: FACTORY_TYPES.FACTORY_UNISWAPV2_ROUTER02_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //历史游戏地址查询 获得路由地址 和 游戏地址
    [FACTORY_TYPES.EVENT_FACTORY_GAMES](id) {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: { id },
        methodName: FACTORY_TYPES.EVENT_FACTORY_GAMES,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //当前游戏配置信息 获得路由地址 和 游戏地址
    [FACTORY_TYPES.EVENT_LAST_ROUTER_NEWSAGE_ADDRESS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: {},
        methodName: FACTORY_TYPES.EVENT_LAST_ROUTER_NEWSAGE_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    getFactory() {
      const that = this;
      let factory = that.factory;
      const promises = [
        //游戏启动次数
        that[FACTORY_TYPES.FACTORY_LAST_ID](),
        //代币地址
        that[FACTORY_TYPES.FACTORY_DETO_ADDRESS](),
        //路由工厂地址
        that[FACTORY_TYPES.FACTORY_NEWSAGE_ROUTER_FACTORY_ADDRESS](),
        //newsage工厂地址
        that[FACTORY_TYPES.FACTORY_NEWSAGE_FACTORY_ADDRESS](),
        //WETH地址
        that[FACTORY_TYPES.FACTORY_WETH_ADDRESS](),
        //uniswap 资金池地址
        that[FACTORY_TYPES.FACTORY_UNISWAPV2_PAIR_ADDRESS](),
        //uniswap 路由地址
        that[FACTORY_TYPES.FACTORY_UNISWAPV2_ROUTER02_ADDRESS](),
        //当前游戏配置信息 获得路由地址 和 游戏地址
        that[FACTORY_TYPES.EVENT_LAST_ROUTER_NEWSAGE_ADDRESS](),
      ];
      return new Promise(function (resolve, reject) {
        Promise.all(promises).then((arr) => {
          factory.lastId = Number(arr[0]);
          factory.DETOAddress = arr[1];
          factory.NewSageRouterFactory = arr[2];
          factory.SmartMatrixNewsageFactory = arr[3];
          factory.WETHAddress = arr[4];
          factory.uniswapV2PairAddress = arr[5];
          factory.uniswapV2Router02Address = arr[6];
          factory.SmartMatrixNewsageAddress =
            arr[7]["SmartMatrixNewsageAddress"];
          factory.NewSageRouterAddress = arr[7]["NewSageRouterAddress"];
          that[ACTION_TYPES.INIT_FACTORY]({ factory });
          arr ? resolve(arr) : reject();
        });
      });
    },
    CallSmartMatrixNewsage(obj) {
      const that = this;
      return new Promise(function (resolve, reject) {
        that.$root.callToAccessBlockchain({
          value: obj.value, //转账值
          requestParams: obj.requestParams, //参数
          contractIndexToUse: obj.contractIndexToUse, //合约名称
          methodName: obj.methodName, //方法名称
          managerIndex: "NewsageManager",
          pairtType: obj.pairtType,
          callback: (isSuccess) => {
            isSuccess != undefined ? resolve(isSuccess) : reject();
          },
        });
      });
    },
    //根据id查询地址
    [NEWSAGE_TYPES.UPDATE_NEWSAGE_ID_TO_ADDRESS](id) {
      const that = this;
      let temObj = {
        contractIndexToUse: "SmartMatrixNewsage", //合约名称
        value: 0,
        requestParams: { id },
        methodName: NEWSAGE_TYPES.UPDATE_NEWSAGE_ID_TO_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //查询用户是否注册
    [NEWSAGE_TYPES.UPDATE_NEWSAGE_IS_USER_EXISTS](Address) {
      const that = this;
      let temObj = {
        contractIndexToUse: "SmartMatrixNewsage", //合约名称
        value: 0,
        requestParams: { Address: Address },
        methodName: NEWSAGE_TYPES.UPDATE_NEWSAGE_IS_USER_EXISTS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    inputvalueChange(event){
      let that = this;
      that.selectAddress = event.target.value;
      if(that.$store.state.web3
            .instance()
            .utils.isAddress(that.selectAddress)){

            that[NEWSAGE_TYPES.UPDATE_NEWSAGE_IS_USER_EXISTS](that.selectAddress).then(res=>{
              if (!res){
                
                 that.displayTips({isTips:true,tipsMs:this.$store.state.language=='CN'?'用户没有注册':'User is not registered'});
                  setTimeout(() => {
                    that.displayTips({isTips:false,tipsMs:''});
                  }, 2500);
              }
            })
              

      }else{
        
         that.getFactory().then(()=>{
            that[NEWSAGE_TYPES.UPDATE_NEWSAGE_ID_TO_ADDRESS](that.selectAddress).then((res) => {
              
              if(res=='0x0000000000000000000000000000000000000000'){
                  that.displayTips({isTips:true,tipsMs:this.$store.state.language=='CN'?'查询编号不存在':'The query number does not exist'});
                  setTimeout(() => {
                    that.displayTips({isTips:false,tipsMs:''});
                  }, 2500);
              }
            })
        })
      }
      
      
    },
  
     
     
      
     
    
    //购买
    [NEWSAGE_TYPES.EVENT_NEWSAGE_BUY_NEW_LEVEL](totalPrice,X3levels,X6levels){
      const that = this;
      
      let temObj={
        value:that.$store.state.web3.instance().utils.toWei(`${totalPrice}`,'ether'),
        requestParams:{X3levels,X6levels},
        methodName:NEWSAGE_TYPES.EVENT_NEWSAGE_BUY_NEW_LEVEL
      };
     return that.CallSmartMatrixNewsage(temObj);
    },
     BUY(X3levels,X6levels,cb){
      let that = this;
      X3levels=that.filterX3Arr(X3levels);//数组过滤
      X6levels=that.filterX6Arr(X6levels);//数组过滤
      let totalPrice=that.taximeter(X3levels,X6levels);

      console.log(totalPrice)
      if(totalPrice>0){
        that[NEWSAGE_TYPES.EVENT_NEWSAGE_BUY_NEW_LEVEL](totalPrice,X3levels,X6levels).then(res=>{
          
          if(cb){cb(res);}
        });
      }
    },
    //注册
    [NEWSAGE_TYPES.EVENT_NEWSAGE_REGIS_TRATION_EXT](isNewTeam,refferrerAddress,totalPrice,X3levels,X6levels){
      const that = this;//
     
      let temObj={
        contractIndexToUse: "SmartMatrixNewsage", //合约名称
        value:that.$store.state.web3.instance().utils.toWei(`${totalPrice}`,'ether'),
        requestParams:{isNewTeam,refferrerAddress,X3levels,X6levels},
        methodName:NEWSAGE_TYPES.EVENT_NEWSAGE_REGIS_TRATION_EXT
      };
     return that.CallSmartMatrixNewsage(temObj);
    },
    btnregistrationExt(isNewTeam,refferrerAddress,X3levels,X6levels,cb){
      let that = this;
      X3levels=that.filterX3Arr(X3levels);//数组过滤
      X6levels=that.filterX6Arr(X6levels);//数组过滤
       console.log(X3levels)
      console.log(X6levels)
       let totalPrice=that.taximeter(X3levels,X6levels);
       console.log(totalPrice)
      if(totalPrice>0){
        that[NEWSAGE_TYPES.EVENT_NEWSAGE_REGIS_TRATION_EXT](isNewTeam,refferrerAddress,totalPrice,X3levels,X6levels).then(res=>{
          
          if(cb){cb(res);}
        });
      }
    },


    clickSignin(){
      let that = this;
      if(that.$store.state.web3
            .instance()
            .utils.isAddress(that.selectAddress)){
             
            that[NEWSAGE_TYPES.UPDATE_NEWSAGE_IS_USER_EXISTS](that.selectAddress).then(res=>{
              if (res) {
               
                that.$router.push(`/Console?selectAddress=${that.selectAddress}`);
              }else{
            
                 that.displayTips({isTips:true,tipsMs:this.$store.state.language=='CN'?'用户没有注册':'User is not registered'});
                  setTimeout(() => {
                    that.displayTips({isTips:false,tipsMs:''});
                  }, 2500);
              }
            })
      }else{
        
         that.getFactory().then(()=>{
            that[NEWSAGE_TYPES.UPDATE_NEWSAGE_ID_TO_ADDRESS](that.selectAddress).then((res) => {
              
              if(res=='0x0000000000000000000000000000000000000000'){
                  that.displayTips({isTips:true,tipsMs:this.$store.state.language=='CN'?'查询编号不存在':'The query number does not exist'});
                  setTimeout(() => {
                    that.displayTips({isTips:false,tipsMs:''});
                  }, 2500);
              }else{
                that.$router.push(`/Console?selectAddress=${res}`);
              }
            })
        })
      }
    },
    clickAuthorization(){
      let that = this;
      that.$router.push(`/Console?selectAddress=${that.$store.state.user.coinbase}`);
    },
    clickGoSignUp(){
      let that = this;
     
      that.$router.push(`/Signup`);
    }
   
  },
};


import { mapActions } from "vuex";
import {
  ACTION_TYPES,
  HTML_SIGIN,
  NEWSAGE_TYPES,
  FACTORY_TYPES
} from "@/util/constants";
import SelectCountry from "@/components/SelectCountry.vue";
</script>

